const getSortedAnime = async (pageNo,perPage,sort)=>{
  // Here we define our query as a multi-line string
// Storing it in a separate .graphql/.gql file is also possible
var query = `
query ($page:Int,$perPage:Int,$sort:[MediaSort]) { 
  Page (page:$page,perPage:$perPage) { 
    pageInfo{
      currentPage
      hasNextPage
    }
    media(format_not_in:[MANGA,NOVEL,ONE_SHOT],sort:$sort){
      id
      description
      title{
        english
        native
        userPreferred
      }
      episodes
      duration
      status
      bannerImage
      coverImage{
        large
        extraLarge
      }
      genres
      season
      seasonYear
      isAdult
    }
  }
}
`;

// Define our query variables and values that will be used in the query request
var variables = {
    page:pageNo,
    perPage:perPage,
    sort:sort
};

// Define the config we'll need for our Api request
var url = 'https://graphql.anilist.co',
    options = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
        },
        body: JSON.stringify({
            query: query,
            variables: variables
        })
    };

// Make the HTTP Api request
const res = await fetch(url, options);
const data = await res.json();

return data.data.Page;

}


const searchAnime = async (option)=>{
var query = `
query ($page:Int,$perPage:Int,$q:String,$isAdult:Boolean,$genres:[String],$sort:[MediaSort]) { 
  Page (page:$page,perPage:$perPage) { 
    pageInfo{
      currentPage
      hasNextPage
    }
    media(genre_in:$genres,sort:$sort,isAdult:$isAdult,format_not_in:[MANGA,NOVEL,ONE_SHOT],search:$q){
      id
      description
      title{
        english
        native
        userPreferred
      }
      episodes
      duration
      status
      bannerImage
      coverImage{
        large
        extraLarge
      }
      genres
      season
      seasonYear
      isAdult
    }
  }
}
`;

// Define our query variables and values that will be used in the query request
var variables = {
    ...option
};

// Define the config we'll need for our Api request
var url = 'https://graphql.anilist.co',
    options = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
        },
        body: JSON.stringify({
            query: query,
            variables: variables
        })
    };

// Make the HTTP Api request
const res = await fetch(url, options);
const data = await res.json();


return data.data.Page;

}

