import React, { useEffect, useState } from 'react'
import Card from './components/Card';
import { searchAnime } from '../api';

export default function SearchPage() {
  const [query,setQuery] = useState({page:1,pageRate:20});
  const [searchResults,setSearchResults] = useState({});

  useEffect(()=>{
    searchAnime(query).then(data=>{
      setSearchResults(data)
    })
  },[query])

  let genres = [
    "Action",
    "Adventure",
    "Comedy",
    "Drama",
    "Ecchi",
    "Fantasy",
    "Hentai",
    "Horror",
    "Mahou Shoujo",
    "Mecha",
    "Music",
    "Mystery",
    "Psychological",
    "Romance",
    "Sci-Fi",
    "Slice of Life",
    "Sports",
    "Supernatural",
    "Thriller",
  ];

  const handleFilter = e=>{
    setSearchResults({})

    let queryInput = document.querySelector("#query");
    let adult = document.querySelector("#isAdult")
    let sort = document.querySelector("#sort")
    let checkBoxList = document.querySelectorAll('.genreCheck');

    let genres = [];

    checkBoxList.forEach(chbox=>{
      if(chbox.checked){
        genres.push(String(chbox.value));
      }
    })

    let filterOptions = {...query};
    if(queryInput.value){
      filterOptions['q'] = queryInput.value;
    }else{
      delete filterOptions.q
    }
    if(adult.value !== "none"){
      filterOptions['isAdult'] = Boolean(adult.value);
    }else{
      delete filterOptions.isAdult
    }
    if(sort.value){
      filterOptions['sort'] = sort.value;
    }else{
      delete filterOptions.sort
    }
    if(genres.length!==0){
      filterOptions['genres'] = genres;
    }else{
      delete filterOptions.genres
    }

    console.log(genres.length);

    setQuery(filterOptions);

  };

  return (
    <>
      <div className="search-wrapper">
        <div className="flex overflow-hidden rounded-lg w-[100%]">
          <input id='query' placeholder='Search...' type="text" className='text-black focus:border-none text-lg px-4 py-2 w-[100%]'/>
          <button onClick={handleFilter} className='text-lg bg-lime-600 px-4 py-2'>Search</button>
        </div>
        <div className="filter-wrapper">
          <h1 className='btn' onClick={(e)=>{
            e.preventDefault()
            document.querySelector('.filter-wrap').classList.toggle("hidden")
          }}>Filter</h1>
          <span className="filter-wrap hidden">
          <ul className='filter'>
            <li className='px-2 py-1 border border-white'>
            <label className='text-white mr-2 font-bold' htmlFor="isAdult">Adult Content</label>
            <select defaultValue='none' name="isAdult" id="isAdult">
              <option value="none">Both</option>
              <option value="true">only 18+</option>
              <option value="false">No 18+</option>
            </select>
            </li>
            <li className='px-2 py-1 border border-white'>
            <label className='text-white mr-2 font-bold' htmlFor="sort">Sort</label>
            <select defaultValue="SEARCH_MATCH" name="sort" id="sort">
              <option value="SEARCH_MATCH">Most Relative</option>
              <option value="POPULARITY_DESC">Popularity (highest to lowest)</option>
              <option value="POPULARITY">Popularity (lowest to highest)</option>
              <option value="UPDATED_AT">Last Update (newest to oldest)</option>
              <option value="UPDATED_AT_DESC">Last Update (oldest to newest)</option>
              <option value="START_DATE">Date Started (oldest to newest)</option>
              <option value="START_DATE_DESC">Date Started (newest to oldest)</option>
            </select>
            </li>
          </ul>
          <ul className="filter genreList">
            {genres.map((genre,i)=>{
              return <li
              key={"filterGenre"+String(i)}
              className='p-2 border hover:bg-[#fcf8f85d] border-white text-white flex gap-1'
              onClick={e=>{
                e.currentTarget.querySelector('.genreCheck').checked = !(e.currentTarget.querySelector('.genreCheck').checked);
              }}
              >
                <input type="checkbox" className='genreCheck' value={genre} name='genreCheck' id={genre} />
                <label htmlFor={genre}>{genre}</label>
              </li>
            })}
            <li><button className='btn text-white' onClick={handleFilter}>Apply</button></li>
          </ul>
          </span>
        </div>
      </div>
      <div className="results">
      <h1 className='before:content-[""] font-black text-4xl my-6 flex gap-[10px] relative before:w-[10px] before:h-full before:bg-lime-600 before:absolute
      before:top-0 before:rounded before:left-0'><span className='ml-[20px]'>Search Results For "{query.q ? query.q : null}"</span></h1>
      <ul className="resultLayout">
            {searchResults.media ? <>{searchResults.media.map((anime,index)=>{
              return <li key={"search"+String(index)}><Card showAdult={true} info={anime}  /></li>
            })}</> : "Loading..."}
      </ul>
      </div>
    </>
  )
}
