import React from "react";
import { Slide } from "hero-slider";
import { capitalize } from "lodash";

export default function slide({ data, rank }) {
  const { bannerImage, title, genres, episodes, duration, status } = data;
  return (
    <Slide
      className="slide"
      background={{
        backgroundAnimation:'fade',
        backgroundAnimationDuration:250,
        backgroundImageSrc:bannerImage
      }}
    >
      <div className="darkshadow" />
      <div className="flex flex-col-reverse gap-4 absolute bottom-6 max-md:left-4 left-8 text-white">
        <h1 className="text-6xl max-md:text-2xl font-semibold capitalize">
          <span className="hidden max-md:inline">#{rank} </span>
          {title.english ? title.english : title.userPreferred}
        </h1>
        <div className="flex gap-1 max-md:hidden">
          {genres
            ? genres.map((item) => {
                return (
                  <div className="px-2 py-1 max-md:text-[10px] max-md:p-1 rounded-md text-lg bg-red-600 capitalize">
                    {item}
                  </div>
                );
              })
            : null}
        </div>
        <div className="flex gap-1">
          <div className="px-2 py-1 max-md:text-[10px] max-md:p-1 rounded-md text-lg bg-red-600">
            {status ? capitalize(status) : null}
          </div>
          <div className="px-2 py-1 max-md:text-[10px] max-md:p-1 rounded-md text-lg bg-lime-600 capitalize">
            {episodes ? episodes : "?"} ep
          </div>
          <div className="px-2 py-1 max-md:text-[10px] max-md:p-1 rounded-md text-lg bg-lime-600 capitalize">
            {duration ? duration : "?"} mins
          </div>
        </div>
        <h1 className="text-xl max-md:hidden text-white">#{rank}</h1>
      </div>
    </Slide>
  );
}
