import React, { useEffect, useState } from 'react';
import HeroSlider from './components/HeroSlider';
import Section from './components/Section';
import {  getGenres, getSortedAnime } from '../api';

export default function HomePage() {
  const [slider,setSlider] = useState([]);
  const [trendData,setTrendData] = useState([]);
  const [popData,setPopData] = useState([]);
  const [latUpData,setLatUpData] = useState([]);
  getGenres().then(data=>{
    console.log(data);
  })

  useEffect(()=>{
    // setSlider(getSortedAnime(1,10,"POPULARITY_DESC").data['Page'].media)
    getSortedAnime(1,10,"FAVOURITES_DESC").then(data=>{
      setSlider(data.media);
    })
    getSortedAnime(1,20,"TRENDING_DESC").then(data=>{
      setTrendData(data.media);
    })
    getSortedAnime(1,20,"POPULARITY_DESC").then(data=>{
      setPopData(data.media);
    })
    getSortedAnime(1,20,"UPDATED_AT").then(data=>{
      setLatUpData(data.media);
    })
  },[])

  

  return (
    <>
      {slider ? <HeroSlider sliderData={slider}/> : null}
      {trendData ? <Section key='trending' head="Trending Anime" media={trendData}/> : null}
      {trendData ? <Section key='popular' head="Popular Anime" media={popData}/> : null}
      {trendData ? <Section key='latest' head="Latestly Updated Anime" media={latUpData}/> : null}
    </>
  )
}
