import { capitalize } from "lodash";
import React from "react";
import { useNavigate } from "react-router-dom";

export default function Card({ info ,showAdult}) {
  const navigate = useNavigate();

  const {
    coverImage,
    title,
    status,
    season,
    seasonYear,
    duration,
    episodes,
    isAdult,
    id
  } = info;

  function formatTitle(t) {
    let tle;
    if (t.english) {
      tle = t.english;
    } else if (t.userPreferred) {
      tle = t.userPreferred;
    }
    if (tle.length > 17) {
      return tle.substring(0, 15) + "...";
    } else {
      return tle;
    }
  }

  return (
    <div onClick={e=>{
      navigate("/anime/"+String(id))
    }} className="aspect-[3/4] relative bg-white shadow-md rounded-lg m-2 overflow-hidden hover:z-10 hover:scale-[1.1] transition-[all_0.5s_ease-in-and-out]">
      <div className="flex flex-col-reverse gap-1 m-1 items-end absolute top-0 right-0">
      {status ? <div className="tag">{capitalize(status).replaceAll("_"," ",)}</div> : null}
      {episodes ? <div className="tag">{episodes} ep</div> : null}
      {duration ? (
        <div className="tag">
          {duration > 60 ? (
            <>
              {duration % 60 !== 0 ? (
                <>
                  {Math.trunc(duration / 60)} hrs {Math.trunc(duration % 60)} mins
                </>
              ) : (
                <>{Math.trunc(duration / 60)} hrs</>
              )}
            </>
          ) : (
            <>{duration} mins</>
          )}
        </div>
      ) : null}
      </div>
      <div className="">
        <img
          draggable={false}
          src={coverImage.large}
          alt="Card"
          className={`w-full h-40 max-lg:h-36 object-cover ${isAdult&&!(showAdult) ? "blur-[4px]" : ""}`}
        />
        <div className="px-4 py-4 text-center max-md:p-[1px] relative">
          <h2 className="text-lg max-md:text-sm font-semibold mb-2 max-md:mb-0">
            {formatTitle(title)}
          </h2>
          <p className="text-gray-600">
            {season} {seasonYear}
          </p>
        </div>
      </div>
    </div>
  );
}
