import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { getAnimeInfo } from "../api";

export default function AnimePage() {
  const { animeId } = useParams();
  const [animeInfo, setAnimeInfo] = useState({});

  const getMonth = (no)=>{
    let monthlist = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];

    return monthlist[no]
  }

  useEffect(() => {
    getAnimeInfo(animeId).then((data) => {
      setAnimeInfo(data);
    });
  }, [animeId]);

  if(Object.keys(animeInfo).length!==0){
    return (
      <>
        <div className="w-full flex items-center justify-center flex-col gap-4">
          <img
            src={animeInfo.coverImage.extraLarge}
            alt=""
            className="aspect-[3/4] max-w-full w-[350px]"
          />
          <div className="w-full font-bold text-lg">
            <h1
              className='before:content-[""] font-black text-4xl my-6 flex gap-[10px] relative before:w-[10px] before:h-full before:bg-lime-600 before:absolute before:top-0 before:rounded before:left-0'>
              <span className="ml-[20px]">Anime Details</span>
            </h1>
            <ul>
              <li className="font-black">Title</li>
              <li><h1>-{animeInfo.title.romaji}</h1></li>
              <li><h1>-{animeInfo.title.english}</h1></li>
              <li><h1>-{animeInfo.title.native}</h1></li>
            </ul>
            <h1><span className="font-black">Genre :</span> {animeInfo.genres.map((genre,i)=>{
              if(i===0){
                return <>{genre}</>
              }else{
                return <>,{genre}</>
              }
            })}</h1>
            <h1><span className="font-black">Duration : </span>{animeInfo.duration}</h1>
            <h1><span className="font-black">Episodes : </span>{animeInfo.episodes}</h1>
            <h1><span className="font-black">Season : </span>{animeInfo.season}||{animeInfo.seasonYear}</h1>
            <h1><span className="font-black">Start Date : </span>{getMonth(animeInfo.startDate.month)} {animeInfo.startDate.day}, {animeInfo.startDate.year}</h1>
            <h1><span className="font-black">End Date : </span>{animeInfo.endDate.year}/{animeInfo.endDate.month}/{animeInfo.endDate.day}</h1>
            <h1><span className="font-black"></span></h1>
          </div>
        </div>
      </>
    );
  }else{
    return <h1 className="font-bold">Loading...</h1>
  }
}
