import React from 'react'
import Card from './Card';
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";

export default function Section({head,media}) {
  const responsive = {
    desktop: {
      breakpoint: { max: 10000, min: 1024 },
      items: 6
    },
    tablet: {
      breakpoint: { max: 1024, min: 768 },
      items: 4
    },
    mobile: {
      breakpoint: { max: 767, min: 0 },
      items: 2
    }
  };


  return (
    <section className='my-4'>
      <h1 className='before:content-[""] font-black text-4xl my-6 flex gap-[10px] relative before:w-[10px] before:h-full before:bg-lime-600 before:absolute
      before:top-0 before:rounded before:left-0'><span className='ml-[20px]'>{head}</span></h1>
      <Carousel
        responsive={responsive}
        autoPlay={false}
        swipeable={true}
        draggable={true}
        showDots={false}
        infinite={false}
        partialVisible={true}
        containerClass='slider'
      >
        {media.map((anime,index)=>{
          return <Card key={10+index} info={anime}/>
        })}
      </Carousel>
    </section>
  )
}
