import React from "react";
import { NavLink } from "react-router-dom";
import MenuImg from "../../img/hamburgerMenu.png";

export default function Navbar() {
  let genres = [
    "Action",
    "Adventure",
    "Comedy",
    "Drama",
    "Ecchi",
    "Fantasy",
    "Hentai",
    "Horror",
    "Mahou Shoujo",
    "Mecha",
    "Music",
    "Mystery",
    "Psychological",
    "Romance",
    "Sci-Fi",
    "Slice of Life",
    "Sports",
    "Supernatural",
    "Thriller",
  ];

  const menu = (oc) => {
    let menuId = document.getElementById("menu");
    if (oc === "o") {
      menuId.classList.remove("hidden");
      menuId.classList.add("block");
    } else if (oc === "c") {
      menuId.classList.add("hidden");
      menuId.classList.remove("block");
    }
  };

  const toggleMenu = (elem) => {
    let ul = document.querySelector(elem);
    ul.classList.toggle("hidden");
    ul.classList.toggle("flex");
  };

  return (
    <>
      <div
        id="menu"
        className="overflow-scroll z-[2147483647] hidden fixed top-0 left-0 h-full w-full bg-gray-600"
      >
        <ul className="w-full flex flex-col mt-3 gap-3 items-center justify-around">
          <li>
            <button
              id="close-menu"
              className="menu-link"
              onClick={(e) => menu("c")}
            >
              x Close Menu
            </button>
          </li>
          <li>
            <NavLink
              className="menu-link"
              onClick={(e) => {
                menu("c");
              }}
              to="/"
            >
              Home
            </NavLink>
          </li>
          <li>
            <NavLink
              className="menu-link"
              onClick={(e) => {
                menu("c");
              }}
              to="/search"
            >
              Search
            </NavLink>
          </li>
          <li className="flex flex-col gap-1">
            <NavLink
              className="menu-link"
              to="/genres"
              onClick={(e) => {
                e.preventDefault();
                toggleMenu(".genre-ul");
              }}
            >
              Genres
            </NavLink>
            <ul className="genre-ul hidden w-full font-bold text-white flex-col gap-1 items-center">
              {genres.map((genre,i) => {
                return (
                  <li key={"navGenre"+String(i)}>
                    <NavLink
                      onClick={(e) => {
                        menu("c");
                      }}
                      to={"/genre/" + genre}
                    >
                      {genre}
                    </NavLink>
                  </li>
                );
              })}
            </ul>
          </li>
        </ul>
      </div>
      <div className="nav-wrapper">
        <div className="navbar">
          <div className="logo">
            Ph<span>anime</span>
          </div>
          <ul className="list-non flex gap-2 max-md:hidden">
            <li>
              <NavLink className="nav-link" to="/">
                Home
              </NavLink>
            </li>
            <li>
              <NavLink className="nav-link" to="/search">
                Search
              </NavLink>
            </li>
            <li>
              <NavLink
                className="nav-link"
                to="/catogories"
                onClick={(e) => e.preventDefault()}
              >
                Catogories
              </NavLink>
            </li>
            <li>
              <NavLink
                className="nav-link"
                to="/genre"
                onClick={(e) => e.preventDefault()}
              >
                Genre
              </NavLink>
            </li>
          </ul>
          <button
            onClick={() => menu("o")}
            className="hidden max-md:block h-[90%]"
          >
            <img
              className="object-contain object-center h-full"
              src={MenuImg}
              alt=""
            />
          </button>
        </div>
      </div>
    </>
  );
}
